import  React,{useRef}from 'react'
import PayByCredit from '../pages/PayByCredit'
import PayByShop from '../pages/PayByShop'
import PayByAtm from '../pages/PayByAtm'
let PaymentCard = function({payment}){
    
    return (
    <>
        {
            ()=>{
                switch(payment){
                    case 'credit': 
                        return   <PayByCredit/>
                        break;
                    case 'shop': 
                        return   <PayByShop/>
                        break;  
                    case 'atm': 
                        return   <PayByAtm/>
                        break;

                }
            }
        }     
    </>)
}

export default PaymentCard